import React, { PureComponent } from 'react'
import { ActivityIndicator, StyleSheet, View } from 'react-native'
import { Logger } from '../../utils/Utils';

export default class LoadingIcon extends PureComponent {
  constructor(props) {
    super(props);

    this.state = props.skinState;
  }

  render() {
    if (!this.state.playbackStatus ||
      typeof this.state.playbackStatus.playableDurationMillis === 'undefined' ||
      this.state.playbackStatus.playableDurationMillis - this.state.playbackStatus.positionMillis < 1000) {
      return (
        <View style={[styles.container, styles.horizontal]}>
          <ActivityIndicator size="large" color="#FFFFFF" />
        </View>
      )
    }
    return null;
  }
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: 'rgba(0, 0, 0, 0.1)',
    flex: 1,
    justifyContent: 'center',
    width: '100%',
    height: '100%'
  },
  horizontal: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    padding: 10
  }
})
